.headersection {
  height: 56px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgb(16, 35, 119);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box;
  z-index: 90;
}



.headersection img {
  height: 80%;
  

}
@media (max-width: 499px) {
  .DesktopOnly {
      display: none;
  }
}